.scroll-up {
	position: fixed;
	right: 16px;
	bottom: 24px;
	width: 52px;
	height: 52px;
	background-color: #fff;
	box-shadow: var(--box-shadow);
	border-radius: 50%;
	display: flex;
	align-items: center;
	justify-content: center;
	cursor: pointer;
	z-index: 30;

	&--hidden {
		opacity: 0;
		visibility: hidden;
		transition: all 300ms;

		@include active {
			opacity: 1;
			visibility: visible;
			transition: all 300ms;
		}
	}

	@include hover {
		.scroll-up__icon {
			stroke: var(--primary-color);
		}
	}

	&__icon {
		width: 24px;
		height: 24px;
		flex: 0 0 24px;
		stroke: var(--icon-default);
		stroke-width: 2px;
		transition: all 300ms;
	}
}
