.news-slider {
	.news-block {
		margin: 8px 0 30px 0;
	}
}

.news-block {
	display: block;
	padding: 24px;
	min-height: calc(216px - 50px);
	border: 1px solid var(--border-color);
	border-radius: var(--default-radius);
	transition: all 300ms;

	@include hover {
		box-shadow: var(--box-shadow);
		border-radius: var(--default-radius);
		border-color: transparent;
		background-color: #fff;
	}

	&__logo {
		display: flex;
		align-items: center;
		margin-bottom: 20px;
		min-height: 68px;
	}

	&__image {
		display: inline-block;
	}

	&__text {
		margin: 0;
		display: block;
		display: -webkit-box;
		font-weight: 400;
		font-size: 18px;
		line-height: 24px;
		color: #000;
		-webkit-line-clamp: 3;
		-webkit-box-orient: vertical;
		overflow: hidden;
		text-overflow: ellipsis;
	}
}
