.toast {
	position: fixed;
	right: 1rem;
	bottom: 1rem;
	z-index: 30;
	--bs-toast-max-width: 355px;
	--bs-toast-bg: rgba(0, 0, 0, 0.8);
	--bs-toast-border-radius: 24px;
	--bs-toast-padding-x: 12px;
	--bs-toast-padding-y: 12px;

	&:not(.showing):not(.show) {
		opacity: 0;
	}

	&.hide {
		display: none;
	}

	&-image {
		display: inline-block;
		margin-right: 20px;
		border-radius: 1rem;
	}
}
