.product-item {
	position: relative;
	padding-top: 12px;
	padding-bottom: 12px;
	background-color: #fff;
	will-change: transform;
	transition: all 300ms;

	@include laptop {
		&:nth-child(-n + 3) {
			padding-top: 0;
		}

		&:not(:nth-child(3n + 3)) {
			border-right: 1px solid var(--border-color);
		}

		&:not(:nth-last-child(-n + 3)) {
			border-bottom: 1px solid var(--border-color);
		}
	}

	@include desktop-xl {
		&:not(:nth-child(3n + 3)) {
			border-right: none;
		}

		&:not(:nth-last-child(-n + 3)) {
			border-bottom: none;
		}
		&:not(:nth-child(4n + 4)) {
			border-right: 1px solid var(--border-color);
		}

		&:not(:nth-last-child(-n + 4)) {
			border-bottom: 1px solid var(--border-color);
		}
	}

	&::after {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: #fff;
		border-radius: 6px;
		z-index: 2;
		transition: all 300ms;
	}

	@include hover {
		border-color: transparent;
		z-index: 2;
		transition: all 300ms;
		&::after {
			transform: scale(1.01);
			transform-origin: center;
			transition: all 300ms;
			box-shadow: var(--box-shadow);
		}
	}
}

.product-cart {
	padding: 16px;
	position: relative;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	height: 100%;
	z-index: 3;

	&__header {
		position: relative;
		width: 100%;
		height: 234px;
		margin-bottom: 12px;
		overflow: hidden;
	}

	&__rating {
		position: absolute;
		top: 0;
		left: 0;
		z-index: 1;
	}

	&__action {
		position: absolute;
		top: 0;
		right: 0;
		z-index: 1;
	}

	&__icon {
		display: block;
		width: 24px;
		height: 24px;
		padding: 0;
		border: none;
		background: transparent;
		&:not(:last-child) {
			margin-bottom: 15px;
		}

		svg {
			width: 100%;
			height: 100%;
			fill: none;
			transition: all 300ms;
		}
	}

	&__like {
		svg {
			fill: transparent;
			stroke: var(--icon-default);
			stroke-width: 2px;
			transition: all 300ms;
		}
		@include hover {
			svg {
				stroke: var(--secondary-color);
			}
		}
		@include active {
			svg {
				stroke: var(--red-color);
				fill: var(--red-color);
			}

			@include hover {
				svg {
					stroke: var(--red-color);
				}
			}
		}
	}

	&__link {
		display: block;
		width: 100%;
		height: 100%;
	}

	&__image {
		display: block;
		width: 100%;
		height: 100%;
		object-fit: contain;
		object-position: center;
		line-height: 234px;
		text-align: center;

		@media (max-width: 1200px) {
			padding-top: 30px;
		}
	}

	&__name {
		margin: 0;
		display: block;
		font-weight: 400;
		font-size: 18px;
		line-height: 22px;
		color: #000;

		&:not(:last-child) {
			margin-bottom: 10px;
		}
	}

	&__company {
		margin: 0;
		font-weight: 400;
		font-size: 16px;
		line-height: 22px;
		color: var(--secondary-color);
	}

	&__body {
		margin-bottom: 22px;
	}

	&__footer {
		margin-top: auto;
		width: 100%;
	}

	&__price {
		margin: 0;
		font-weight: 600;
		font-size: 24px;
		line-height: 24px;
		&:not(:last-child) {
			margin-bottom: 10px;
		}
	}

	&__count {
		position: relative;
		font-weight: 400;
		font-size: 14px;
		line-height: 22px;
		color: var(--secondary-color);
		display: inline-flex;
		align-items: center;
		justify-content: center;

		&::before {
			content: "";
			display: inline-block;
			margin-right: 8px;
			width: 6px;
			height: 6px;
			border-radius: 50%;
			background-color: var(--primary-color);
		}
	}

	&__buttons {
		margin-top: 20px;
		display: grid;
		grid-template-columns: 1fr 1fr;
		grid-gap: 5px;

		@include desktop {
			grid-gap: 8px;
		}

		.button {
			width: 100%;
		}
	}

	&__adding {
		display: none;

		&.button--loading {
			&::after {
				border-color: var(--icon-default) var(--icon-default)
					transparent;
			}

			.card-adding--minus:after,
			.card-adding--plus:after,
			.card-adding--plus::before {
				opacity: 0.5;
			}

			.card-adding__input {
				color: transparent;
			}
		}
	}
}
