.tab {
	&__header {
		margin-bottom: rem(24px);
		display: flex;
		align-items: flex-start;
	}

	&__links {
		display: flex;
		flex-wrap: wrap;
		row-gap: 6px;
		margin-right: 6px;
	}

	&__link {
		display: inline-block;
		padding: rem(8px) rem(12px);
		flex: 0 0 max-content;
		font-weight: 400;
		font-size: rem(18px);
		line-height: rem(24px);
		color: var(--secondary-color);
		border-radius: 8px;
		transition: all 300ms;

		&:not(:first-child) {
			margin-left: 6px;
		}

		@include active {
			color: #000;
			background-color: var(--bg-light);
		}

		@include hover {
			color: var(--secondary-color);
			background-color: var(--bg-light);
		}
	}

	&__block {
		display: none;

		@include active {
			display: block;
			animation: fade-in 0.5s linear both;
		}
	}
}
