.article-item {
	display: block;
	width: 100%;
	height: 100%;
	&__link {
		display: flex;
		align-items: flex-start;
		flex-direction: column;
		width: 100%;
		height: 100%;
		border-radius: 24px;
		transition: all 300ms;

		@include hover {
			box-shadow: var(--box-shadow);
		}
	}

	&__header {
		position: relative;
		width: 100%;
		flex: 0 0 auto;
		min-height: 234px;
		aspect-ratio: 16/9;
		border-radius: var(--default-radius);
		overflow: hidden;
	}

	&__image {
		position: absolute;
		display: block;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		object-fit: cover;
		object-position: center;
	}

	&__block {
		padding: 16px 24px 24px;
		display: flex;
		align-items: flex-start;
		flex-direction: column;
		width: 100%;
		height: 100%;
	}

	&__tag {
		margin: 0;
		font-weight: 400;
		font-size: rem(16px);
		line-height: rem(22px);
		color: var(--text-dark);

		&:not(:last-child) {
			margin-bottom: 4px;
		}
	}

	&__name {
		margin: 0;
		display: block;
		display: -webkit-box;
		font-weight: 600;
		font-size: rem(20px);
		line-height: rem(24px);
		color: #000;
		-webkit-line-clamp: 3; /*   */
		-webkit-box-orient: vertical;
		overflow: hidden;
		text-overflow: ellipsis;
		&:not(:last-child) {
			margin-bottom: 1rem;
		}
	}

	&__footer {
		margin-top: auto;
		display: flex;
		align-items: center;
		justify-content: space-between;
		width: 100%;
	}

	&__date {
		font-weight: 400;
		font-size: rem(14px);
		line-height: rem(22px);
		color: var(--secondary-color);
	}

	&__info {
		display: flex;
		align-items: center;
	}

	&__time,
	&__view {
		display: flex;
		align-items: center;
		font-weight: 400;
		font-size: rem(14px);
		line-height: rem(20px);
		color: var(--secondary-color);
	}

	&__view {
		margin-left: 1rem;
	}

	&__icon {
		display: inline-block;
		margin-right: 4px;
		width: 20px;
		height: 20px;
		flex: 0 0 20px;
		fill: none;
	}
}
