.rating {
	display: flex;
	align-items: center;
	width: max-content;

	&__block {
		padding: 4px 6px 4px 4px;
		display: flex;
		align-items: center;
		justify-content: center;
		background: var(--bg-light);
		border-radius: 8px;
		cursor: pointer;
	}

	&__icon {
		display: inline-block;
		margin-right: 4px;
		width: 18px;
		height: 18px;
	}

	&__count {
		margin-left: 8px;
		font-size: 14px;
		line-height: 22px;
		color: var(--secondary-color);
		cursor: pointer;

		@include hover {
			color: var(--text-dark);
		}
	}
}
