.pharmacy {
	&-slider {
		position: relative;

		.pharmacy-block {
			margin: 12px 0 40px;
		}

		&__navigation {
			&--prev,
			&--next {
				top: calc(50% - 20px);
			}
		}
	}
	&-block {
		display: block;
		border: 1px solid var(--border-color);
		border-radius: var(--default-radius);
		overflow: hidden;
		max-width: 196px;
		height: 98px;
		transition: all 300ms;

		@include hover {
			border-color: transparent;
			box-shadow: var(--box-shadow);
			border-radius: var(--default-radius);
			transition: all 300ms;
		}

		&__image {
			display: block;
			width: 100%;
			height: 100%;
			object-fit: contain;
			object-position: center;
			line-height: 98px;
			text-align: center;
		}
	}
}
