.category {
	&-slider {
		position: relative;
		&__link {
			padding: 16px;
			height: 156px;
			position: relative;
			display: block;
			background-color: var(--bg-light);
			border-radius: 24px;
			overflow: hidden;
			background-size: 100%;
			background-position: bottom center;
			background-repeat: no-repeat;

			@media (max-width: 1200px) {
				height: auto;
				aspect-ratio: 1/1;
			}
		}

		&__name {
			margin: 0;
			font-weight: 400;
			font-size: 18px;
			line-height: 22px;
			color: #000;
			word-break: break-word;
			position: relative;
			z-index: 1;

			@media (max-width: 1200px) {
				font-size: 14px;
				line-height: 16px;
			}

			&:not(:last-child) {
				margin-bottom: 10px;
			}
		}

		&__image {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			object-fit: cover;
			object-position: center;
		}

		.swiper-slide {
			box-sizing: border-box;
		}
	}
}
