.ui-section {
	&__item {
		margin-top: rem(32px);
	}

	&__title {
		margin: 0;
		font-weight: 600;
		font-size: rem(40px);
		line-height: rem(44px);
	}

	&__text {
		margin-top: rem(12px);
		margin-bottom: 0;
		font-weight: 400;
		font-size: rem(18px);
		line-height: rem(24px);
		color: var(--text-dark);
	}

	&__h2 {
		margin: 0;
		font-weight: 600;
		font-size: rem(24px);
		line-height: rem(32px);
	}

	&__list {
		padding-left: rem(10px);
		margin-left: rem(10px);
		li {
			margin-top: rem(12px);
			list-style: disc;
			font-weight: 400;
			font-size: rem(18px);
			line-height: rem(24px);
			color: var(--text-dark);
		}
	}
}
