.hero {
	margin-bottom: 24px;
	&__slider {
		position: relative;
	}
	&__slide {
		position: relative;
		margin-top: 3rem;
		padding: 46px 56px 48px;
		min-height: 300px;
		background-color: #f7f7fa;
		border-radius: 32px;
	}

	&__text {
		position: relative;
		z-index: 2;
	}

	&__title {
		margin: 0;
		font-weight: 600;
		@include css-lock(30, 64, 320, 1440);
		@include css-lock(30, 56, 320, 1440, line-height);

		&:not(:last-child) {
			margin-bottom: 19px;
		}
	}

	&__subtitle {
		margin: 0;
		font-weight: 600;
		@include css-lock(20, 24, 320, 1440);
		@include css-lock(24, 28, 320, 1440, line-height);

		&:not(:last-child) {
			margin-bottom: 19px;
		}
	}

	&__image {
		position: absolute;
		bottom: 0;
		right: 43px;
	}

	&__button {
		margin-top: 40px;
	}

	&__advertising {
		position: absolute;
		right: 16px;
		bottom: 16px;
	}
}

.swiper-slide-active {
	.hero__title {
		animation: fade-in-bottom 0.8s cubic-bezier(0.39, 0.575, 0.565, 1) both;
	}

	.hero__subtitle {
		animation: fade-in-bottom 0.8s 0.2s cubic-bezier(0.39, 0.575, 0.565, 1)
			both;
	}

	.hero__button {
		animation: fade-in-bottom 0.8s 0.3s cubic-bezier(0.39, 0.575, 0.565, 1)
			both;
	}

	.hero__image {
		animation: fade-in-right 0.8s cubic-bezier(0.39, 0.575, 0.565, 1) both;
	}
}

.advertising {
	@include hover {
		.advertising__block {
			opacity: 1;
			visibility: visible;
			transition: all 300ms;
		}
	}

	&__text {
		padding: 3px 12px;
		font-size: rem(12px);
		line-height: rem(22px);
		color: #444952;
		background: rgba(255, 255, 255, 0.8);
		border-radius: 99px;
		cursor: pointer;
	}

	&__block {
		position: absolute;
		right: 0;
		bottom: 35px;
		padding: 20px 24px 24px;
		min-width: 360px;
		background-color: #fff;
		box-shadow: var(--box-shadow);
		border-radius: 24px 24px 16px 24px;
		opacity: 0;
		visibility: hidden;
		transition: all 300ms;
		z-index: 2;

		&::after {
			content: "";
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: calc(100% + 20px);
		}
	}

	&__title {
		margin-bottom: 8px;
		font-weight: 600;
		font-size: rem(20px);
		line-height: rem(24px);
		color: #000;
	}

	&__subtitle {
		font-weight: 400;
		font-size: rem(16px);
		line-height: rem(20px);
		color: #444952;

		&:not(:last-child) {
			margin-bottom: rem(12px);
		}
	}

	&__link {
		position: relative;
		margin: 0;
		display: inline-flex;
		align-items: center;
		font-weight: 600;
		font-size: rem(14px);
		line-height: rem(20px);
		color: var(--primary-color);
		z-index: 2;

		&:not(:last-child) {
			margin-bottom: 12px;
		}

		@include hover {
			color: var(--primary-hover-color);
		}

		&--complete {
			display: none;
		}
	}

	&__icon {
		display: inline-block;
		margin-right: 6px;
		width: 20px;
		height: 20px;
		flex: 0 0 20px;
		background-position: center;
		background-repeat: no-repeat;

		&--earth {
			background-image: url("../images/icons/Earth.svg");
		}

		&--copy {
			background-image: url("../images/icons/copy.svg");
		}

		&--checked {
			background-image: url("../images/icons/check.svg");
		}
	}
}

.info {
	&__title {
		&:not(:last-child) {
			margin-bottom: rem(48px);
		}
	}

	&-block {
		&__image {
			margin-bottom: 1rem;
		}
		&__text {
			margin: 0;
			font-weight: 400;
			font-size: rem(18px);
			line-height: rem(24px);
		}
	}
}

.reserved-block {
	&__text {
		font-weight: 400;
		font-size: rem(18px);
		line-height: rem(24px);
		color: #000;
	}

	&__line {
		position: absolute;
		top: 74px;
		left: 0;
		right: 0;
		width: 684px;
		height: 96px;
		margin: auto;
		background: url("../images/icons/reserved-line.svg") no-repeat center;
		z-index: -1;
	}
}

.ibox {
	&__item {
		display: flex;
		&:nth-child(even) {
			flex-direction: row-reverse;
		}
	}

	&__title {
		margin: 0;
		font-weight: 600;
		font-size: rem(24px);
		line-height: rem(32px);

		&:not(:last-child) {
			margin-bottom: rem(8px);
		}
	}

	&__subtitle {
		margin: 0;
		font-weight: 400;
		font-size: rem(18px);
		line-height: rem(24px);

		&:not(:last-child) {
			margin-bottom: 0.5rem;
		}
	}
}

.product-slider,
.article-slider {
	position: relative;
	.swiper-wrapper {
		box-sizing: border-box;
	}

	.swiper-slide {
		height: auto;
	}
}

.article-slider {
	.swiper-slide {
		margin-top: 30px;
		margin-bottom: 2rem;
	}
}

.ui-home-section {
	&:not(:last-child) {
		margin-bottom: rem(56px);
	}
}

.information {
	&:not(:last-child) {
		margin-bottom: rem(56px);
	}
	&__block {
		padding: rem(32px);
		font-weight: 400;
		font-size: rem(14px);
		line-height: rem(24px);
		color: var(--text-dark);
		background-color: var(--bg-light);
		border-radius: rem(32px);
	}
}

.find-section {
	&__title {
		&:not(:last-child) {
			margin-bottom: rem(20px);
		}
	}

	&__item {
		display: flex;
		flex-wrap: wrap;
		gap: 4px;

		@media (min-width: 1200px) and (max-width: 1400px) {
			gap: 3px;
		}
	}

	&__link {
		display: inline-block;
		padding: 4px 10px;
		min-width: 32px;
		font-weight: 500;
		font-size: rem(18px);
		line-height: rem(24px);
		text-align: center;
		color: var(--secondary-color) !important;
		border-radius: 8px;
		text-transform: uppercase;
		transition: all 300ms;

		@include hover {
			background-color: var(--bg-light);
			transition: all 300ms;
		}
	}

	&__last {
		margin-left: rem(28px);

		@media (min-width: 1200px) and (max-width: 1400px) {
			margin-left: 0;
		}
	}
}

.news-slider {
	position: relative;
}
